import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import ApprasialGeo                from "./ApprasialGeo";

const PARAMS = { select_fields : ["geo_address", "general_information"] };
const DEFAULT_FETCH_DATA = {  geo_address : {}, general_information : {} } ;

const AppraisalGeoContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id } = useParams();
	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { geo_address } = appraise;

	// ------------> useHookForm <---------------
	const defaultValues  = {
		...geo_address,
		viality_references : geo_address?.viality_references ??  [...Array(3).keys()].map( () => ({
			viality_type : undefined,
			clave        : "",
			name         : "",
		})),
	};

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;

	// ------------> submitData <---------------
	const onSubmit = handleSubmit(async (data) => {
		await editApprasial({
			module : `/appraisals/geo-address/${id}`,
			method : "PUT",
			data   : {
				...data,
				zip_code : data.zip_code !== "" ? data.zip_code : undefined,
			},
			tags : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<ApprasialGeo
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
				data     	: appraise,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(AppraisalGeoContainer, PARAMS);
