import { GeoForm, HeaderPage, Tabs } from "Components/Appraisal";

const tabs = [
	{
		Tab   : GeoForm,
		title : "Domicilio GeogrÃ¡fico del Inmueble",
	},
];

const ApprasialGeo = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
		data,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={2}
				section="header"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>

			<Tabs
				tabs={tabs}
				onSubmit={onSubmit}
				hookForm={hookForm}
				isSubmitingData={isLoading}
				data={data}
			/>
		</>
	);
};

export default ApprasialGeo;
